import { Component, Element, Event, EventEmitter, Host, Prop, Watch, h } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';

const SCROLL_AMOUNT = 200;

const isHTMLMdxTabElementElement = (element: string | HTMLMdxTabElementElement): element is HTMLMdxTabElementElement => {
  if (typeof element === 'object' && typeof element.tab === 'string') {
    return true;
  }

  return false;
};

/**
 * @slot - Slot for tab elements
 */
@Component({
  tag: 'mdx-tab-bar',
  styleUrl: 'tab-bar.scss',
  shadow: true,
})
export class TabBar {
  @Prop()
  fillWidth: boolean;

  @Element()
  host: HTMLMdxTabBarElement;

  @Prop({ reflect: true })
  selectedTab: HTMLMdxTabElementElement['tab'];

  @Watch('selectedTab')
  onSelectedTabChanged() {
    this.selectedTabChanged.emit(this.selectedTab);
  }

  @Event()
  selectedTabChanged: EventEmitter<HTMLMdxTabElementElement['tab']>;

  private toggleChevronVisibility = () => {
    const ul = this.host.shadowRoot?.querySelector('.tab-bar__tabs');
    const chevronLeft = this.host.shadowRoot?.querySelector<HTMLElement>('.tab-bar__chevron__left');
    const chevronRight = this.host.shadowRoot?.querySelector<HTMLElement>('.tab-bar__chevron__right');
    const currentScroll = Math.ceil((ul?.getBoundingClientRect()?.width || 0) + (ul?.scrollLeft || 0));

    if (!ul || !chevronLeft || !chevronRight) {
      return;
    }

    if (ul?.scrollLeft > 0) {
      chevronLeft.style.visibility = 'visible';
    } else {
      chevronLeft.style.visibility = 'hidden';
    }

    if (currentScroll < ul.scrollWidth) {
      chevronRight.style.visibility = 'visible';
    } else {
      chevronRight.style.visibility = 'hidden';
    }
  };

  private onClickChevronRight = () => {
    const ul = this.host.shadowRoot?.querySelector('.tab-bar__tabs');
    ul?.scroll({
      left: ul.scrollLeft + SCROLL_AMOUNT,
      behavior: 'smooth',
    });

    setTimeout(() => this.toggleChevronVisibility(), 300);
  };

  private onClickChevronLeft = () => {
    const ul = this.host.shadowRoot?.querySelector('.tab-bar__tabs');
    ul?.scroll({
      left: ul.scrollLeft - SCROLL_AMOUNT,
      behavior: 'smooth',
    });

    setTimeout(() => this.toggleChevronVisibility(), 300);
  };

  private select(tab: string) {
    if (this.selectedTab === tab) {
      return false;
    }
    this.selectedTab = tab;
    return true;
  }

  private get tabs() {
    return Array.from(this.host.querySelectorAll('mdx-tab-element'));
  }

  private getTab(tab: string | HTMLMdxTabElementElement): HTMLMdxTabElementElement | undefined {
    return isHTMLMdxTabElementElement(tab) ? tab : this.tabs.find((t) => t.tab === tab);
  }

  componentWillLoad() {
    if ((!this.selectedTab || !this.getTab(this.selectedTab)) && this.tabs.length > 0) {
      this.select(this.tabs[0].tab);
    }
  }

  componentWillRender() {
    const selectedTab = this.getTab(this.selectedTab);
    this.tabs.forEach((tab) => {
      tab.active = false;
    });
    if (selectedTab) {
      selectedTab.active = true;
    }
  }

  componentDidRender() {
    this.toggleChevronVisibility();
  }

  render() {
    return (
      <Host
        onTabClick={({ detail: { tab } }) => {
          this.select(tab);
        }}
      >
        <div class="tab-bar">
          <mdx-icon-chevron-left
            onClick={this.onClickChevronLeft}
            class="tab-bar__chevron tab-bar__chevron__left"
            size={ICON_SIZES.XS}
          />
          <div class="tab-bar__tabs">
            <slot></slot>
          </div>
          <mdx-icon-chevron-right
            onClick={this.onClickChevronRight}
            class="tab-bar__chevron tab-bar__chevron__right"
            size={ICON_SIZES.XS}
          />
        </div>
      </Host>
    );
  }
}
